:root {
    --min-height: 80vh;
    --top-gap: 3.25vw;
    --right-gap: 6.51vw;
    --left-gap: 6.51vw;
    --fontsize: 2.8rem;
    --sub-heading: #90acd1;
    --main-font: #222222;
    --special-font: #e45447;
    --background: #e1f5fe;
    --light-heading: #777777;
  }
  
  .Home {
    background-color: var(--background);
  }
  
  /* grid */
  .container {
    display: grid;
    height: auto;
    padding-bottom: 10vh;
  }
  /* when on desktop or larger screens */
  @media screen and (min-width: 960px) {
    .container {
      grid-template-columns: 50% 50%;
      height: auto;
      padding-bottom: 10vh;
    }
  }
  
  .header {
    padding-top: var(--top-gap);
    padding-bottom: var(--top-gap);
    padding-right: var(--right-gap);
  }
  
  .image {
    justify-content: center;
    text-align: center;
    padding-top: var(--top-gap);
  }
  @media screen and (min-width: 960px) {
    .image {
      padding-left: 200px;
    }
  }
  
  /* Hey, I am text */
  .subheading {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: bold;
    color: var(--sub-heading);
    letter-spacing: 4px;
  }
  
  /* Masudha Meher name */
  .myName {
    font-size: var(--fontsize);
    font-weight: bolder;
    font-family: "Poppins", sans-serif;
    color: var(--main-font);
    display: block;
  }
  
  /* animated text */
  h2 {
    color: var(--special-font);
    font-weight: 400;
    font-size: 30px;
    line-height: 42px;
  }
  
  /* two liner into */
  .introduction {
    margin-bottom: 45px;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    color: var(--main-font);
    opacity: 0.8;
    font-weight: 200;
    font-size: 18px;
  }
  /* contact me button */
  .contact-me {
    background: var(--sub-heading);
    text-transform: capitalize;
    color: #fff;
    cursor: pointer;
    display: block;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 2px;
    line-height: 0;
    padding: 30px 44px;
    border-radius: 60px;
    transition: color 0.4s linear;
    position: relative;
    z-index: 1;
    border: 0;
    overflow: hidden;
  }
  
  /* animated circle */
  .hero-shape {
    position: absolute;
    top: 13%;
    left: 51%;
  }
  
  .custom-animation {
    backface-visibility: hidden;
    animation: jumping 9s ease-in-out 2s infinite alternate;
    animation-duration: 9s;
    animation-timing-function: ease-in-out;
    animation-delay: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-fill-mode: none;
    animation-play-state: running;
    animation-name: jumping;
    animation-delay: 1s;
    transition: all 7s ease 2s;
    user-select: none;
  }
  
  @keyframes jumping {
    0% {
      transform: translateY(10px) translateX(0) rotate(0) scale(1);
      opacity: 0.9;
    }
    20% {
      transform: translateY(20px) translateX(10px) rotate(-20deg) scale(0.8);
      opacity: 0.8;
    }
    40% {
      transform: translateY(15px) translateX(-15px) rotate(10deg) scale(0.75);
      opacity: 0.8;
    }
    40% {
      transform: translateY(-15px) translateX(-25px) rotate(10deg) scale(0.5);
      opacity: 1;
    }
    80% {
      transform: translateY(-30px) translateX(20px) rotate(-20deg) scale(0.75);
      opacity: 0.6;
    }
    100% {
      transform: translateY(15px) translateX(15px) rotate(0) scale(0.95);
      opacity: 0.7;
    }
  }
  
  